.header {
   position: absolute;
   background: #f2f3f4;
   height: 160px;
   width: 100%;
}

.title {
   text-align: center;
   font-size: 35px;
   font-family: Arial, Helvetica, sans-serif;
}

.algorithm-button-container {
   position: absolute;
   left: 20px;
}

.algorithm-button {
   margin: 0px 3px 0px 0px;
   font-family: Arial, Helvetica, sans-serif;
   transition-duration: 0.4s;
   background-color: #add8e6;
   font-size: 18px;
   padding: 16px;
   border: 3px solid transparent;
}

.algorithm-button:hover {
   border: 3px solid #325C74;
   cursor: pointer;
}

.create-array-button {
   position: absolute;
   left: 711px;
   font-family: Arial, Helvetica, sans-serif;
   transition-duration: 0.4s;
   background-color: #b19cd9;
   font-size: 18px;
   padding: 16px;
   border: 3px solid transparent;
}

.create-array-button:hover {
   border: 3px solid #8A2BE2;
   cursor: pointer;
}

.start-sorting-button {
   position: absolute;
   left: 895px;
   font-family: Arial, Helvetica, sans-serif;
   transition-duration: 0.4s;
   background-color: #90ee90;
   font-size: 18px;
   padding: 16px;
   border: 3px solid transparent;
}

.start-sorting-button:hover {
   border: 3px solid #007600;
   cursor: pointer;
}

.array-container {
   position: absolute;
   top: 160px;
   right: 20px;
   left: 20px;
}

.array-bar {
   padding-top: 7px;
   font-family: Arial, Helvetica, sans-serif;
   text-align: center;
   display: inline-block;
   margin: 0 1px;
   vertical-align: top;
}

.slider-container {
   position: absolute;
   right: 335px;
   top: 90px;
}

.num-elements {
   position: absolute;
   transition-duration: 0.4s;
   cursor: pointer;
}

.num-elements-text {
   position: absolute;
   font-family: Arial, Helvetica, sans-serif;
   top: 10px;
   left: 10px;
   width: 125px;
}

.sorting-speed {
   position: absolute;
   left: 175px;
   transition-duration: 0.4s;
   cursor: pointer;
}

.sorting-speed-text {
   position: absolute;
   font-family: Arial, Helvetica, sans-serif;
   width: 110px;
   left: 190px;
   top: 10px;
}
